import CommApi from "@/pages/comm/api.js"
import moment from '@/pages/comm/moment-with-locales.min.js'

export default {
	data() {
		return {
			teamId: null,
			teamObj: {},
			moment,
			userinfo: {
				id: null,
				openid: null,
				academic: null,
				studnumber: null,
				name: null,
				telephone: null,
				grade: null
			},
			openid: null,
		}
	},
	
	created() {
		this.userinfo = uni.getStorageSync("userinfo")
		this.openid = this.userinfo && this.userinfo.openid;
		this.teamId = this.$route.query && this.$route.query.id;
		this.getDetail();
	},
	
	methods: {
		getDetail() {
			uni.request({
				url: CommApi.api.getSubTeamDetail.real + '/' + this.teamId,
				data: {
				},
				method: 'GET',
				header: {
					'content-type': 'application/json',
					"Access-Control-Allow-Origin": '*',
				},
				success: (res) => {
					if (res.statusCode === 200 && res.data.code === 1) {
						console.log(res.data.data)
						this.teamObj = res.data.data;
					} else {
						uni.showToast({
							title: res.data && res.data.msg || res.data.error,
							icon: "none"
						});
					}
				}
			});
		},
		joinActive() {
			uni.showModal({
				title: '报名确认',
				content: '是否参加报名',
				success: (res) => {
					if (res.confirm) {
						uni.request({
							url: CommApi.api.joinActive.real,
							data: JSON.stringify({
								openid: this.openid || 0,
								hdid: this.teamId,
							}),
							method: 'POST',
							header: {
								'content-type': 'application/json',
								"Access-Control-Allow-Origin": '*',
							},
							success: (res) => {
								if (res.statusCode === 200 && res.data.code === 1) {
									WeixinJSBridge.call('closeWindow');
								} else {
									uni.showToast({
										title: res.data && res.data.msg || res.data.error,
										icon: "none"
									});
								}
							}
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
	}
}
